@font-face {
  font-family: 'MS UI Gothic';
  src: url('./fonts/MS-UIGothic.ttf') format('truetype');
  font-weight: 100;
  font-smooth: never;
  -webkit-font-smoothing: none;
  -moz-osx-font-smoothing: grayscale;
}

html {
  -webkit-text-size-adjust: 100%; /* For Safari */
  text-size-adjust: 100%; /* For Chrome, Firefox, Edge, Opera */
  font-family: 'MS UI Gothic', sans-serif; /* Set font for everything */
}

body {
  background-color: #000;
  filter: contrast(1.1) saturate(1.4) brightness(0.9);
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
  font-family: 'MS UI Gothic', sans-serif;
}

.vhs-effect {
  position: relative;
  overflow: hidden;
}

[class*="border"] {
  background-color: #000;
  box-shadow: 
    0 0 1px var(--glow-color, currentColor),
    0 0 2px var(--glow-color, currentColor),
    0 0 3px var(--glow-color, currentColor);
  transition: box-shadow 0.3s ease;
}

h1, h2, h3, p, span {
  text-shadow: 0 0 10px currentColor, 0 0 30px currentColor, 0 0 50px currentColor;
}

.scanlines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    transparent 50%,
    rgba(32, 178, 170, 0.15) 50%
  );
  opacity: 20%;
  background-size: 100% 8px;
  z-index: 1;
  pointer-events: none;
}